import React, { useState, useEffect, useContext } from 'react';
import { WatchListContext } from '../Contexts/WatchListContext'
import { SearchContext } from '../Contexts/SearchContext'
import useFetch from '../useFetch';
import Spinner from 'react-bootstrap/Spinner';
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';

const Movies = () => {

    const { data, loading, isErr } = useFetch([
        `https://api.themoviedb.org/3/list/7077601?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`,
        `https://api.themoviedb.org/3/list/7078334?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`,
        `https://api.themoviedb.org/3/list/7078244?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
    ]);

    const { watchList, handleClick } = useContext(WatchListContext);
    const { searchInput } = useContext(SearchContext)


    const [moviePoster, setmoviePoster] = useState(`giphy (1).gif`);
    const [movieTitle, setmovieTitle] = useState('');
    const [movieDescription, setmovieDescription] = useState('');
    const [summary, setSummary] = useState('')

    const stopAtPeriod = () => {
        const summary = movieDescription.split('.');
        setSummary(summary[0])
    }

    useEffect(() => {
        stopAtPeriod()
    }, [movieDescription])



    const styles = {
        backgroundImage: `url(${moviePoster})`
    };


    SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

    return (

        <div className='show-container'>
            {isErr && <div className="error">{isErr}</div>}
            {loading && <Spinner animation="border" variant="secondary" className="spinner" >
                <span>Loading...</span>
            </Spinner>}
            <div className='hero'>
                <div className='show-hero' style={styles}></div>
                <div className="contains-descriptions">
                    <h2 className="hero-show-title show-movie">{movieTitle}</h2>
                    <p className="hero-show-description show-movie">{summary}</p>
                </div>
            </div>
            <section className="movies">
                <h2 style={{ color: 'white', marginLeft: '20px' }}>Action </h2>
                {data && <Swiper
                    spaceBetween={10}
                    slidesPerView={6}
                    pagination={{ clickable: true }}
                    scrollbar={{ draggable: true }}
                >
                    {data && data[0].items.filter(item => {
                        if (searchInput === '') {
                            return item
                        } else if (item.original_title.toLowerCase().includes(searchInput.toLowerCase())) {

                            return <div className="filtered" >{item}</div>
                        }
                    }).map(movie =>
                        <SwiperSlide key={movie.id}>
                            <div className='movie' >
                                <img onMouseOver={() => {
                                    setmoviePoster(`${"https://image.tmdb.org/t/p/original" + movie.poster_path}`);
                                    setmovieTitle(movie.original_title);
                                    setmovieDescription(movie.overview);
                                }}
                                    src={'https://image.tmdb.org/t/p/original' + movie.poster_path} width='250px' height='300px'
                                    alt='Promotional Poster For Movie'
                                />
                                <button className="watchlist-btn"
                                    onClick={() => handleClick(movie.original_title)}>
                                    {watchList.includes(movie.original_title) ?
                                        <i className="fas fa-minus-circle"></i> :
                                        <i className="fas fa-plus-circle"></i>}
                                </button>
                            </div>
                        </SwiperSlide>
                    )
                    }
                </Swiper>}
            </section>

            <section className="movies">
                <h2 style={{ color: 'white', marginLeft: '20px' }}>Comedy</h2>
                {data && <Swiper
                    spaceBetween={10}
                    slidesPerView={4}
                    pagination={{ clickable: true }}
                    scrollbar={{ draggable: true }}
                >
                    {data && data[1].items.filter(item => {
                        if (searchInput === '') {
                            return item
                        } else if (item.original_title.toLowerCase().includes(searchInput.toLowerCase())) {
                            return (

                                <div className="movie" >{item}</div>

                            )
                        }
                    }).map(movie =>
                        <SwiperSlide key={movie.id}>
                            <div className='movie' >
                                <img onMouseOver={
                                    () => {
                                        setmoviePoster(`${"https://image.tmdb.org/t/p/original" + movie.poster_path}`);
                                        setmovieTitle(movie.original_title);
                                        setmovieDescription(movie.overview);
                                    }}
                                    src={'https://image.tmdb.org/t/p/original' + movie.poster_path} width='250px' height='300px'
                                    alt='Promotional Poster For Movie'
                                />
                                <button className="watchlist-btn"
                                    onClick={() => handleClick(movie.original_title)}>
                                    {watchList.includes(movie.original_title) ?
                                        <i className="fas fa-minus-circle"></i> :
                                        <i className="fas fa-plus-circle"></i>}
                                </button>
                            </div>
                        </SwiperSlide>
                    )
                    }
                </Swiper>}
            </section>

            <section className='movies'>
                <h2 style={{ color: 'white', marginLeft: '20px' }}>Award-Winning</h2>
                {data && <Swiper
                    spaceBetween={10}
                    slidesPerView={4}
                    pagination={{ clickable: true }}
                    scrollbar={{ draggable: true }}

                >
                    {data && data[2].items.filter(item => {
                        if (searchInput === '') {
                            return item
                        } else if (item.original_title.toLowerCase().includes(searchInput.toLowerCase())) {

                            return <div className="filtered" >{item}</div>
                        }
                    }).map(movie =>
                        <SwiperSlide key={movie.id}>
                            <div className='movie' >
                                <img onMouseOver={
                                    () => {
                                        setmoviePoster(`${"https://image.tmdb.org/t/p/original" + movie.poster_path}`);
                                        setmovieTitle(movie.original_title);
                                        setmovieDescription(movie.overview);
                                    }}
                                    src={'https://image.tmdb.org/t/p/original' + movie.poster_path}
                                    width='250px' height='300px' alt='Promotional Poster For Movie'
                                />

                                <button className="watchlist-btn"
                                    onClick={() => handleClick(movie.original_title)}>
                                    {watchList.includes(movie.original_title) ?
                                        <i className="fas fa-minus-circle"></i> :
                                        <i className="fas fa-plus-circle"></i>}
                                </button>
                            </div>
                        </SwiperSlide>
                    )
                    }
                </Swiper>}
            </section>
        </div >
    )
}

export default Movies
